
.container__personaje {
    background-color: #716844ac;
    color: white;
    max-width: 220px;
    padding: 20px 30px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    height: 600px;
}

    .container__personaje  img{
        border-radius: 50%;
        width: 150px;
        height: 150px;
        object-fit: cover;
        margin: auto;
    }

    .container__personaje  h2{
        font-size: 30px;
        margin: 20px;
        text-align: center;
        border-bottom: 1px solid white;
    }

    .container__personaje  p{
        padding-bottom: 10px;
    }